@import url(https://fonts.googleapis.com/css?family=Patrick+Hand);
body {
  margin: 0;
  padding: 0;
  font-family: 'Patrick Hand', cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
}
.App {
  text-align: center;
  background: #B3FFAB;  /* fallback for old browsers */
background: -webkit-linear-gradient(to right, #12FFF7, #B3FFAB);  /* Chrome 10-25, Safari 5.1-6 */
background: -webkit-gradient(linear, left top, right top, from(#12FFF7), to(#B3FFAB));
background: -webkit-linear-gradient(left, #12FFF7, #B3FFAB);
background: linear-gradient(to right, #12FFF7, #B3FFAB); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
height: 100vh;
width: 100%;
}

h1 {
  text-align: center;
  color: rgb(43, 110, 70);
  font-family: cursive;
  font-size: 74px;
  padding-top: 100px;
}
button {
  font-family: 'Patrick Hand', cursive;
  border-radius: 20px;
  padding: 20px;
  margin-top: 60px;
  margin-right: 20px;
  color: white;
  font-size: 20px;
  background: #12FFF7;
  box-shadow: inset 0px 0px 3pc black;
  border: none;
}
button:focus {
  outline:none;
}
button:active {
  box-shadow: inset 0px 0px 5px #000;
}
