* {
  margin: 0 auto;
  padding: 0;
}
.Header {
  height: 30px;
  width: 100vw;
  background: -webkit-gradient(linear, left top, right top, from(#12FFF7), to(#B3FFAB));
  background: -webkit-linear-gradient(left, #12FFF7, #B3FFAB);
  background: linear-gradient(to right, #12FFF7, #B3FFAB); 
}
h2 {
  text-align: center;
  color: rgb(43, 110, 70);
  font-family: cursive;
  font-size: 34px;
  padding-top: 15px;
}


.Main:active {
  box-shadow: inset 0px 0px 5px #000;
}
.Main {
  display: inline-block;
  border-radius: 50%;
  height: 170px;
  width: 170px;
  margin: 20px;
  background: #42c692;
  box-shadow: inset 0px 0px 10px #000;
  margin-top: 6em;
}
.Main:focus {
  outline:none;
}
div.Main.active {
  background-color: rgb(0, 161, 212);
}


#win {
  display: inline-block;
  height: 200px;
  width: 385px;
  background-color: darkgrey;
  border-radius: 20px;
  position: absolute;    
  left: 36%;
  top: 30%;
  margin-left: 20px;
}
#win p {
  color: black;
  font-size: 20px;
  padding: 20px;
}
#win body {
  background: rgb(102, 112, 129);
}
#close {
  margin: 3em;
  border: none;
}
#overlay {
position: fixed;
top: 0;
left: 0;
width: 100%;
height: 100%;
background-color: rgba(0, 0, 0, 0.7);  /* transparency here the last one... */
display: flex;
justify-content: center;
align-items: center;
}

